.teacher {
  padding-bottom: 130px;
}

.teacher img {
  display: block;
  max-width: 100%;
  height: auto;
  margin-right: 30px;
}

.teacher-title {
  margin-top: 0px;
  margin-bottom: 50px;

  font-style: normal;
  font-weight: 800;
  font-size: 46px;
  line-height: 130%;
  letter-spacing: 0.03em;
  color: $primary-title-color;
}

.teacher-flex {
  display: flex;
  width: calc((100% - 30) / 2);
  position: relative;
}

.teacher-name {
  margin-top: 0px;
  margin-bottom: 15px;
  font-style: normal;
  font-weight: 800;
  font-size: 30px;
  line-height: 37px;
  letter-spacing: 0.08em;
  color: $primary-title-color;
  // position: relative;
}

.teacher-experience {
  margin-top: 30px;
  margin-bottom: 63px;
  padding: 0px;
}

.teacher-description::before {
  position: absolute;
  content: '';
  display: block;
  border: 1px solid $accent-color;
  width: 20px;
  top: 240px;
  left: 560px;
}
.teacher-name::after {
  position: absolute;
  content: '';
  display: block;
  top: 45px;
  left: 560px;
  width: 55%;
  border: 1px solid #b9b9b9;
}

.teacher-experience .item {
  display: flex;
  list-style: none;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.06em;
  color: $primary-title-color;
  &:not(:last-child) {
    margin-bottom: 30px;
  }
}
.teacher-description {
  margin-top: 0px;
  margin-bottom: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  color: $primary-text-color;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.link-Certificate {
  &:hover,
  &:focus {
    color: $accent-color;
  }
}
.galka {
  margin-right: 35px;
  fill: $accent-color;
}
